$green: #8eb059;

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 23px;
    padding-bottom: 21px;
    background-color: $green;
}

.btn__text {
    font-size: 0.875rem;
    text-transform: uppercase;
    font-weight: 700;
    color: #fff;
    text-align: center;
}